import React, { useEffect, useState } from "react";
import { Table, Button } from "react-bootstrap";
import { MdDelete } from "react-icons/md";
import "./cart.css";

function Cart() {
  const [cart, setCart] = useState([]);

  const showCartProducts = cart.map((product, index) => (
    <tr key={index} className="showCart">
      <td>
        <img
          className="product-pic"
          src={"http://localhost:4000/" + product.imagexs}
          alt="none"
        />
      </td>
      <td>
        <p>{product.name}</p>
      </td>
      <td>
        <p>{product.price.toFixed(2)} €</p>
      </td>
      <td>
        <p>{product.quantity}</p>
      </td>
      <td>
        <p>{product.price.toFixed(2) * product.quantity} €</p>
      </td>
      <td>
        <MdDelete
          id="cart-icon"
          onClick={() => deleteProduct(product.productId)}
        />
      </td>
    </tr>
  ));
  /**
   * TVA / TTC part
   */
  let ttc = [];

  const total = cart.map((product, index) =>
    ttc.push(product.price * product.quantity)
  );

  let tvaTotal = [];

  /**
   *  5.5 %
   */
  let tvaFiveFive = [];
  /**
   *  10 %
   */
  let tvaTen = [];
  /**
   *  20 %
   */
  let tvaTwenty = [];

  const calculTva = cart.map((product, index) => {
    let price = [product.price * product.quantity];
    let tvaPrice = price / product.tva;

    if (product.tva === 1.055) {
      return tvaFiveFive.push(price - tvaPrice);
    } else if (product.tva === 1.1) {
      return tvaTen.push(price - tvaPrice);
    } else if (product.tva === 1.2) {
      return tvaTwenty.push(price - tvaPrice);
    }
  });

  const calculTotalTva = cart.map((product, index) => {
    let price = [product.price * product.quantity];
    let tvaPrice = price / product.tva;

    return tvaTotal.push(price - tvaPrice);
  });

  const totalTtc = ttc.map((li) => li).reduce((sum, val) => sum + val, 0);

  const totalTvaFiveFive = tvaFiveFive
    .map((li) => li)
    .reduce((sum, val) => sum + val, 0);

  const totalTvaTen = tvaTen.map((li) => li).reduce((sum, val) => sum + val, 0);

  const totalTvaTwenty = tvaTwenty
    .map((li) => li)
    .reduce((sum, val) => sum + val, 0);

  const totalTva = tvaTotal.map((li) => li).reduce((sum, val) => sum + val, 0);

  useEffect(() => {
    getCart();
  }, []);

  function getCart() {
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "bearer " + localStorage.getItem("token"),
      },
    };

    fetch("http://localhost:4000/account/cart", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (data) => {
          setCart(data);
        },
        (error) => {
          console.log(error);
        }
      );
  }

  const deleteProduct = (id) => {
    const options = {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: "bearer " + localStorage.getItem("token"),
      },
    };

    fetch("http://localhost:4000/account/cart?productId=" + id, options)
      .then((response) => {
        return response.json();
      })
      .then(
        (data) => {
          alert(data.message);
          getCart();
        },
        (error) => {
          console.log(error);
        }
      );
  };

  return (
    <div className="vcenter">
      <div className="column-left">
        <Table className="center-table" striped bordered hover>
          <thead>
            <tr>
              <th></th>
              <th>Produit</th>
              <th>Prix</th>
              <th>Quantité</th>
              <th>Sous-total</th>
              <th>Supprimer</th>
            </tr>
          </thead>
          <tbody>{showCartProducts}</tbody>
        </Table>
      </div>
      <div className="column-right">
        <div className="column-flex">
          <div className="column-split">
            <p>Frais de port :</p>
            <hr />
            <p>TVA 5.5% :</p>
            <hr />
            <p>TVA 10% :</p>
            <hr />
            <p>TVA 20% :</p>
            <hr />
            <p>Total tva :</p>
            <hr />
            <p>TTC :</p>
            <hr />
          </div>
          <div className="column-split">
            <p>Retrait en magasin</p>
            <hr />
            <p>{totalTvaFiveFive.toFixed(2)} €</p>
            <hr />
            <p>{totalTvaTen.toFixed(2)} €</p>
            <hr />
            <p>{totalTvaTwenty.toFixed(2)} €</p>
            <hr />
            <p>{totalTva.toFixed(2)} €</p>
            <hr />
            <p>{totalTtc.toFixed(2)} €</p>
            <hr />
          </div>
        </div>
        <div className="column-button">
          <Button>Passer ma commande</Button>
        </div>
      </div>
    </div>
  );
}

export default Cart;
