import React, { useEffect, useState } from "react";
import "./boutique.css";
import { Col, Row } from "react-bootstrap";
import ProductRow from "../../assets/Components/ProductRow/ProductRow";
import { Link } from "react-router-dom";
import { FaArrowAltCircleRight } from "react-icons/fa";

function BoutiqueBouquets() {
  const [bouquets, setBouquets] = useState([]);

  useEffect(() => {
    getBouquetsProducts();
  }, []);

  function getBouquetsProducts() {
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    };

    fetch("http://localhost:4000/home?category=bouquets", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (products) => {
          setBouquets(products);
        },
        (error) => {
          console.log(error);
        }
      );
  }

  return (
    <section>
      <div className="boutique-container">
        <Row>
          <Col>
            <h2>
              Bouquets
              <Link to="/">
                <FaArrowAltCircleRight />
              </Link>
            </h2>
            <ProductRow products={bouquets} />
          </Col>
        </Row>
      </div>
    </section>
  );
}

export default BoutiqueBouquets;
