import React, { useState } from "react";
import "./account.css";
import { Link, useHistory } from "react-router-dom";
import { Button } from "react-bootstrap";

function Account(props) {
  const [newAccount, setnewAccount] = useState({});

  const [account, setAccount] = useState({});
  const history = useHistory();

  const [modal, setModal] = useState(false);

  const handleNewAccount = (e) => {
    setnewAccount({ ...newAccount, [e.target.name]: e.target.value });
  };

  const addAccount = (e) => {
    e.preventDefault();

    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(newAccount),
    };
    fetch("http://localhost:4000/account/create", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (data) => {
          alert(data.message);
        },
        (error) => {
          console.log(error);
        }
      );
  };

  const handleAccount = (e) => {
    setAccount({ ...account, [e.target.name]: e.target.value });
  };
  const loggingIn = (e) => {
    e.preventDefault();

    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(account),
    };
    fetch("http://localhost:4000/account/login", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (data) => {
          alert(data.message);
          if (data.token) {
            localStorage.setItem("token", data.token);
            props.handleCloseLogin();
            history.push("/");
            document.getElementById("connection").style.display = "none";
            document.getElementById("myAccount").style.display = "block";
          }
        },
        (error) => {
          console.log(error);
        }
      );
  };

  function displayModalInfo() {
    if (modal === false) {
      return (
        <div className="border-none">
          <h1>Connexion</h1>
          <input
            type="email"
            id="email"
            name="email"
            onChange={handleAccount}
            value={account.email}
            required
            placeholder="email"
          ></input>
          <input
            type="password"
            id="password"
            name="password"
            onChange={handleAccount}
            value={account.password}
            required
            placeholder="mot de passe"
          ></input>
          <div className="button-login">
            <Button type="submit" onClick={loggingIn}>
              Se connecter
            </Button>
          </div>
          <p>Vous n'avez pas encore de compte ?</p>
          <Link
            replace
            onClick={() => {
              setModal(!modal);
            }}
          >
            Créez-en un ici
          </Link>
        </div>
      );
    } else if (modal) {
      return (
        <div className="border-none">
          <h1>Création</h1>
          <input
            type="email"
            id="email"
            placeholder=" Email"
            onChange={handleNewAccount}
            name="email"
            value={newAccount.email}
          ></input>
          <input
            type="password"
            id="password"
            placeholder=" Mot de passe"
            onChange={handleNewAccount}
            name="password"
            value={newAccount.password}
          ></input>
          <div className="button-login">
            <Button onClick={addAccount}>S'inscrire</Button>
          </div>
          <p>Vous possédez déjà un compte ?</p>
          <Link
            replace
            onClick={() => {
              setModal(!modal);
            }}
          >
            Connectez-vous ici
          </Link>
        </div>
      );
    }
  }

  return <div className="account-body">{displayModalInfo()}</div>;
}
export default Account;
