import React, { useEffect } from "react";
import "./App.css";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  HashRouter,
} from "react-router-dom";
import Header from "./Components/Header/Header";
import Home from "./Components/Home/Home";
import Account from "./Components/Account/Account";
import Cart from "./Components/Cart/Cart";
import Boutique from "./Components/Boutique/Boutique";
import Content from "./Components/Content/Content";
import Footer from "./Components/Footer/Footer";
import Products from "./Components/Products/Products";
import Moncompte from "./Components/Moncompte/Moncompte";
import Admin from "./Components/Admin/Admin";
import "bootstrap/dist/css/bootstrap.min.css";
import BoutiqueCompositions from "./Components/Boutique/Compositions";
import BoutiqueFetes from "./Components/Boutique/Fetes";
import BoutiqueBouquets from "./Components/Boutique/Bouquets";
import BoutiqueDeuils from "./Components/Boutique/Deuils";
import BoutiqueMariages from "./Components/Boutique/Mariages";
import BoutiquePlantes from "./Components/Boutique/Plantes";
import ScrollUp from "./assets/ScroolUp/ScrollUp";

function App() {
  useEffect(() => {
    showLoggin();
  }, []);

  function showLoggin() {
    let nom = localStorage.getItem("token");
    if (nom) {
      document.getElementById("connection").style.display = "none";
      document.getElementById("myAccount").style.display = "block";
    } else {
      document.getElementById("myAccount").style.display = "none";
      document.getElementById("connection").style.display = "block";
    }
  }
  return (
    <HashRouter basename="/">
      <Header />
      <div className="main-content">
        <ScrollUp>
          <Switch>
            <Route path="/" component={Home} exact />
            <Route path="/account" component={Account} exact />
            <Route path="/moncompte" component={Moncompte} exact />
            <Route path="/cart" component={Cart} exact />
            <Route path="/boutique" component={Boutique} exact />
            <Route path="/content" component={Content} exact />
            <Route path="/product" component={Products} exact />
            <Route path="/admin" component={Admin} exact />
            <Route path="/bouquets" component={BoutiqueBouquets} exact />
            <Route
              path="/compositions"
              component={BoutiqueCompositions}
              exact
            />
            <Route path="/deuils" component={BoutiqueDeuils} exact />
            <Route path="/fetes" component={BoutiqueFetes} exact />
            <Route path="/mariages" component={BoutiqueMariages} exact />
            <Route path="/plantes" component={BoutiquePlantes} exact />
          </Switch>
        </ScrollUp>
      </div>
      <Footer />
    </HashRouter>
  );
}

export default App;
