import React, { useState, useEffect } from "react";
import "./moncompte.css";
import { Button, Card, Col, Row } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import { FcPhone, FcFeedback, FcRules } from "react-icons/fc";
import { CgLogOff } from "react-icons/cg";
import { FiEdit, FiBox } from "react-icons/fi";
import { RiAccountBoxFill } from "react-icons/ri";
import { HiOutlineMail } from "react-icons/hi";
import { MdPayment } from "react-icons/md";

function Moncompte() {
  const history = useHistory();
  const [tabOrder, setTabOrder] = useState("");
  const [user, setUser] = useState("");

  const [showEditInfos, setShowEditInfos] = useState(false);
  const [infos, setInfos] = useState([]);

  const handleSelect = (value) => {
    setTabOrder(value);
  };

  const handleInfos = (e) => {
    setInfos({ ...infos, [e.target.name]: e.target.value });
  };

  useEffect(() => {
    getUser();
  }, []);

  function getUser() {
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "bearer " + localStorage.getItem("token"),
      },
    };

    fetch("http://localhost:4000/account/profile", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (data) => {
          setUser(data);
        },
        (error) => {
          console.log(error);
        }
      );
  }

  const editInfos = (e) => {
    e.preventDefault();
    const options = {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: "bearer " + localStorage.getItem("token"),
      },
      body: JSON.stringify(infos),
    };

    fetch("http://localhost:4000/account/profile", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (data) => {
          alert(data.message);
          getUser();
          setShowEditInfos(false);
        },
        (error) => {
          console.log(error);
        }
      );
  };

  function displayEditInfos() {
    if (showEditInfos) {
      return (
        <Card className="moncompte-card">
          <Card.Body className="display-edit-right">
            <Card.Text>
              <div className="label-input">
                <label>Nom :</label>
                <br />
                <input
                  type="text"
                  name="lastname"
                  placeholder={user.lastname}
                  value={infos.lastname}
                  onChange={handleInfos}
                ></input>
              </div>
              <div className="label-input">
                <label>Prénom :</label>
                <br />
                <input
                  type="text"
                  name="firstname"
                  placeholder={user.firstname}
                  value={infos.firstname}
                  onChange={handleInfos}
                ></input>
              </div>
              <div className="label-input">
                <label>Tél :</label>
                <br />
                <input
                  type="tel"
                  name="phone"
                  placeholder={user.phone}
                  value={infos.phone}
                  onChange={handleInfos}
                ></input>
              </div>
              <div className="label-input">
                <label>Adresse :</label>
                <br />
                <input
                  type="text"
                  name="address"
                  placeholder={user.address}
                  value={infos.address}
                  onChange={handleInfos}
                ></input>
              </div>
              <div className="label-input">
                <label>Code Postal :</label>
                <br />
                <input
                  type="text"
                  placeholder={user.postcode}
                  name="postcode"
                  value={infos.postcode}
                  onChange={handleInfos}
                ></input>
              </div>
              <div className="label-input">
                <label>Ville :</label>
                <br />
                <input
                  type="text"
                  placeholder={user.city}
                  name="city"
                  value={infos.city}
                  onChange={handleInfos}
                ></input>
              </div>
              <div className="label-input">
                <label>Email :</label>
                <br />
                <input
                  type="email"
                  id="email"
                  placeholder={user.email}
                  name="email"
                  value={infos.email}
                  onChange={handleInfos}
                ></input>
              </div>
              <div className="label-input">
                <label>Mot de passe :</label>
                <br />
                <input
                  type="password"
                  id="password"
                  placeholder="Nouveau mot de passe"
                  name="password"
                  value={infos.password}
                  onChange={handleInfos}
                ></input>
              </div>
            </Card.Text>
            <div className="moncompte-link">
              <Button onClick={editInfos}>Valider</Button>
            </div>
          </Card.Body>
        </Card>
      );
    }
  }

  function Tabselect() {
    switch (tabOrder) {
      case "order":
        return (
          <div className="title-moncompte">
            <div class="breadcrumb">
              <p>Dashboard / Commandes</p>
            </div>
            <div className="moncompte-orders">
              <h2>Commandes</h2>
            </div>
          </div>
        );

      case "payment":
        return (
          <div className="title-moncompte">
            <div class="breadcrumb">
              <p>Dashboard / Paiements</p>
            </div>
            <div className="moncompte-paiements">
              <h2>
                Paiements <FiEdit />
              </h2>
            </div>
          </div>
        );

      case "infos":
        return (
          <div className="title-moncompte">
            <div class="breadcrumb">
              <p>Dashboard / Infos perso</p>
            </div>
            <div className="moncompte-infos">
              <h2>Infos perso</h2>
              <Row>
                <Col>
                  <Card className="moncompte-card">
                    <Card.Body className="display-edit">
                      <Card.Text>
                        <p>Nom : {user.lastname}</p>
                        <p>Prénom : {user.firstname}</p>
                        <p>Tél : {user.phone}</p>
                        <p>Adresse : {user.address}</p>
                        <p>Code postal : {user.postcode}</p>
                        <p>Ville : {user.city}</p>
                        <p>Email: {user.email}</p>
                      </Card.Text>
                      <div className="moncompte-link">
                        <Button
                          onClick={() => {
                            setShowEditInfos(!showEditInfos);
                          }}
                        >
                          Modifier
                        </Button>
                      </div>
                    </Card.Body>
                  </Card>
                </Col>
                <Col>{displayEditInfos()}</Col>
              </Row>
            </div>
          </div>
        );

      case "contact":
        return (
          <div className="title-moncompte">
            <div class="breadcrumb">
              <p>Dashboard / Contact</p>
            </div>
            <Row>
              <Col>
                <Card className="moncompte-card">
                  <Card.Img variant="top" src="/0.jpg" />
                  <Card.Body>
                    <Card.Title>
                      <h2>Nous Contacter</h2>
                    </Card.Title>
                    <Card.Text>
                      <div className="moncompte-contact">
                        <p>
                          <FcPhone className="fcicone" /> Tel: +33000000000
                        </p>
                        <p>
                          <FcFeedback className="fcicone" /> Email:
                          e-commerce@lebocal.academy
                        </p>
                        <p>
                          <FcRules className="fcicone" /> e-commerce
                        </p>
                        <p>18 rue du bocal</p>
                        <p>06000 Nice</p>
                      </div>
                    </Card.Text>
                  </Card.Body>
                </Card>
              </Col>
              <Col className="contact-form">
                <div className="form-contact">
                  <Card className="moncompte-card">
                    <Card.Img variant="top" src="/map.jpg" />
                    <Card.Body>
                      <Card.Text>
                        <form
                          action="mailto:xxx@xxx.fr subject=form"
                          name="envoi"
                          method="post"
                          encType="text/plain"
                        >
                          <input
                            type="text"
                            placeholder="Nom"
                            name="nom"
                            id="nom"
                            required
                          />
                          <input
                            type="email"
                            placeholder="Email"
                            name="email"
                            id="email"
                            required
                          />
                          <input
                            type="tel"
                            placeholder="Téléphone"
                            name="tel"
                            id="tel"
                            required
                          />
                          <textarea
                            type="text"
                            placeholder="Message"
                            name="message"
                            id="message"
                          ></textarea>
                          <div className="form-button">
                            <Button type="submit" value="envoyer">
                              Envoyer
                            </Button>
                          </div>
                        </form>
                      </Card.Text>
                    </Card.Body>
                  </Card>
                </div>
              </Col>
            </Row>
          </div>
        );

      default:
    }
  }

  function Disconnect() {
    let nom = localStorage.getItem("token");
    if (nom) {
      localStorage.removeItem("token");
      history.push("/");
      document.getElementById("myAccount").style.display = "none";
      document.getElementById("connection").style.display = "block";
    }
  }

  return (
    <div className="moncompte-body">
      <Row>
        <Col className="background-col">
          <div className="monCompte-nav">
            <h3
              onClick={() => {
                handleSelect("order");
              }}
            >
              <FiBox /> Commandes
            </h3>
            <h3
              onClick={() => {
                handleSelect("infos");
              }}
            >
              <RiAccountBoxFill /> Infos perso
            </h3>
            <h3
              onClick={() => {
                handleSelect("payment");
              }}
            >
              <MdPayment /> Paiements
            </h3>
            <h3
              onClick={() => {
                handleSelect("contact");
              }}
            >
              <HiOutlineMail /> Nous Contacter
            </h3>
            <h3 onClick={Disconnect}>
              <CgLogOff /> Déconnexion
            </h3>
          </div>
        </Col>
        <Col>
          <div className="tabSelect">{Tabselect()}</div>
        </Col>
      </Row>
    </div>
  );
}

export default Moncompte;
