import React, { useEffect, useState } from "react";
import "./boutique.css";
import { Col, Row } from "react-bootstrap";
import ProductRow from "../../assets/Components/ProductRow/ProductRow";
import { Link } from "react-router-dom";
import { FaArrowAltCircleRight } from "react-icons/fa";

function Accueil() {
  const [mangas, setMangas] = useState([]);
  const [videoGames, setVideoGames] = useState([]);
  const [goodies, setGoodies] = useState([]);

  useEffect(() => {
    getHomeProducts();
  }, []);

  function getHomeProducts() {
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    };

    fetch("http://localhost:4000/home?category=mangas", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (products) => {
          setMangas(products);
        },
        (error) => {
          console.log(error);
        }
      );

    fetch("http://localhost:4000/home?category=videoGames", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (products) => {
          setVideoGames(products);
        },
        (error) => {
          console.log(error);
        }
      );

    fetch("http://localhost:4000/home?category=goodies", options)
      .then((response) => {
        return response.json();
      })
      .then(
        (products) => {
          setGoodies(products);
        },
        (error) => {
          console.log(error);
        }
      );
  }

  return (
    <section>
      <div className="boutique-container">
        <Row>
          <Col>
            <h2>
              Bouquets
              <Link to="/bouquets">
                <FaArrowAltCircleRight />
              </Link>
            </h2>
            <ProductRow products={mangas} />
            <hr />
            <h2>
              Plantes
              <Link to="/plantes">
                <FaArrowAltCircleRight />
              </Link>
            </h2>
            <ProductRow products={videoGames} />
            <hr />
            <h2>
              Compositions
              <Link to="/compositions">
                <FaArrowAltCircleRight />
              </Link>
            </h2>
            <ProductRow products={goodies} />
            <hr />
            <h2>
              Fêtes
              <Link to="/fetes">
                <FaArrowAltCircleRight />
              </Link>
            </h2>
            <ProductRow products={goodies} />
            <hr />
            <h2>
              Mariages
              <Link to="/mariages">
                <FaArrowAltCircleRight />
              </Link>
            </h2>
            <ProductRow products={goodies} />
            <hr />
            <h2>
              Deuils
              <Link to="/deuils">
                <FaArrowAltCircleRight />
              </Link>
            </h2>
            <ProductRow products={goodies} />
          </Col>
        </Row>
      </div>
    </section>
  );
}

export default Accueil;
