import React from "react";
import "./home.css";
import { Button, Col, Row } from "react-bootstrap";

function Home() {
  return (
    <section>
      <div className="accueil-background">
        <img src="0.jpg" alt="" />
      </div>

      <div className="home-container">
        <h1>Qui sommes-nous</h1>
        <div className="home-desc">
          <img src="1.jpg" alt="" />
          <p>
            Boutique de Fleurs fleurit votre quotidien selon vos envies et les
            événements de votre vie ! Passionnée par son métier de fleuriste,
            Béatrice, artisan fleuriste et son équipe, vous accueilleront et
            vous conseilleront avec passion et créativité.
            <br />
            <br />
            Nous ne voulons pas simplement vous vendre des fleurs, nous voulons
            partager un instant de calme et de poésie avec vous.
            <br />
            <br />
            Nous essayons de changer régulièrement nos vitrines et d’apporter
            des variantes dans le décor suivant les saisons et les événements
            importants.
          </p>
        </div>
        <div className="home-galery">
          <h1>Galerie</h1>
          <Row className="galery">
            <Col>
              <img src="2.jpg" alt="" />
            </Col>
            <Col>
              <img src="2.jpg" alt="" />
            </Col>
            <Col>
              <img src="2.jpg" alt="" />
            </Col>
            <Col>
              <img src="2.jpg" alt="" />
            </Col>
          </Row>
          <Row className="galery">
            <Col>
              <img src="3.jpg" alt="" />
            </Col>
            <Col>
              <img src="3.jpg" alt="" />
            </Col>
            <Col>
              <img src="3.jpg" alt="" />
            </Col>
            <Col>
              <img src="3.jpg" alt="" />
            </Col>
          </Row>
        </div>
        <div className="home-location">
          <h1>Nous contacter</h1>
          <Row id="address-container">
            <Col>
              <Row id="address">
                <h3>Boutique Machin</h3>
                <p>3 avenue des fleurs</p>
                <p>06000 Nice</p>
              </Row>
              <Row>
                <img src="map.jpg" alt="" />
              </Row>
            </Col>
            <Col className="home-form">
              <form
                action="mailto:xxx@xxx.fr subject=form"
                name="envoi"
                method="post"
                encType="text/plain"
              >
                <input
                  type="text"
                  placeholder="Nom"
                  name="nom"
                  id="nom"
                  required
                />
                <input
                  type="email"
                  placeholder="Email"
                  name="email"
                  id="email"
                  required
                />
                <input
                  type="tel"
                  placeholder="Téléphone"
                  name="tel"
                  id="tel"
                  required
                />
                <textarea
                  type="text"
                  placeholder="Message"
                  name="message"
                  id="message"
                ></textarea>
                <div className="form-button">
                  <Button type="submit" value="envoyer">
                    Envoyer
                  </Button>
                </div>
              </form>
            </Col>
          </Row>
        </div>
      </div>
    </section>
  );
}

export default Home;
